'use strict';
// 红包


// {
// 	toolType:'',  //支付工具类型
//     couponNo:'', //红包编号
//     couponAmount:'' //红包金额
//  }
import {WinJSDec, objError} from './APP/config';

var name = 'USE_COUPON';
var USE_COUPON = obj => {
  if (obj !== 'object') {
    throw new TypeError(`[${name}] ${objError}`);
  }
  WinJSDec(_ => {
    window.WinJSBridge.call('coupon', 'useCoupon', obj);
  });
};
export default USE_COUPON;
